raul-popover {
  display: inline-block;
}

raul-popover .r-popover {
  top: 0;
  left: 0;
  position: absolute;
  display: block;
  background-color: #fff;
  border-radius: 0.125rem;
  border-width: 1px;
  border-style: solid;
  border-color: #EBEDEE;
  box-shadow: 0 16px 27px 0 rgba(82, 97, 115, 0.22);
  transition: opacity .15s linear;
  opacity: 0;
  pointer-events: none;
  z-index: 9999;
}

raul-popover .r-popover--open {
  opacity: 1;
  pointer-events: auto;
  max-width: 320px;
}

raul-popover-image {
  display: block;
  overflow: hidden;
  border-top-left-radius: 0.125rem;
  border-top-right-radius: 0.125rem;
}

raul-popover-image .r-popover__image {
  height: 120px;
  min-width: 318px;
}

raul-popover-content {
  padding: 2rem;
  display: flex;
  position: relative;
}

raul-popover-label {
  display: flex;
  flex-direction: column;
}

raul-popover-col {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
}

raul-popover-controller[x-placement^=top] > raul-popover-arrow {
  width: 0;
  height: 0;
  position: absolute;
  bottom: -11px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 10px solid #EBEDEE;
}

raul-popover-controller[x-placement^=top] > raul-popover-arrow:after {
  content: " ";
  width: 0;
  height: 0;
  position: absolute;
  top: -11px;
  left: -7px;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-top: 9px solid #fff;
}

raul-popover-controller[x-placement^=bottom] > raul-popover-arrow {
  width: 0;
  height: 0;
  position: absolute;
  top: -11px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 10px solid #EBEDEE;
}

raul-popover-controller[x-placement^=bottom] > raul-popover-arrow:after {
  content: " ";
  width: 0;
  height: 0;
  position: absolute;
  bottom: -11px;
  left: -7px;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-bottom: 9px solid #fff;
}

raul-popover-controller[x-placement^=left] > raul-popover-arrow {
  width: 0;
  height: 0;
  position: absolute;
  right: -11px;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-left: 10px solid #EBEDEE;
}

raul-popover-controller[x-placement^=left] > raul-popover-arrow:after {
  content: " ";
  width: 0;
  height: 0;
  position: absolute;
  top: -7px;
  left: -11px;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 9px solid #fff;
}

raul-popover-controller[x-placement^=right] > raul-popover-arrow {
  width: 0;
  height: 0;
  position: absolute;
  left: -11px;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-right: 10px solid #EBEDEE;
}

raul-popover-controller[x-placement^=right] > raul-popover-arrow:after {
  content: " ";
  width: 0;
  height: 0;
  position: absolute;
  top: -7px;
  left: 2px;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-right: 9px solid #fff;
}